    import back from '@/components/back'
	import noData from '@/components/noData'
    export default {
        components: {
			back,
			noData
        },
        data () {
            return {
				id: this.$route.query.id,
				datas: {},
                user: this.$store.state.userInfo.Data,
                mdt: this.$route.query.mdt || ''
            }
		},
		created () {
			this.queryMDT()
		},
		methods: {
			queryMDT () {
				this.$get('MDTAPI/MDT/QueryMDTBySubjectBuyRecordId',{
					SubjectBuyRecordId: this.id
				}).then( m => {
					this.datas = m
				})
			},
			toIM () {
                if(!this.datas.IMGroupId) {
                    this.$toast('不存在该群聊！')
                }else {
                    this.$bridge.callhandler('toGroupChat', this.datas.IMGroupId)
                }
                
            },
            preViewPdf () {
                if (this.datas.SubjectReportPath) {
                        let obj = {
                        title : '报告详情',
                        linkUrl : this.datas.SubjectReportPath
                    }
                    this.$bridge.callhandler('showPdfReport', JSON.stringify(obj))
                }else {
                    this.$toast('SubjectReportPath地址不存在！')
                }
            }
		},
    }



